import React, { useState } from "react";
import { Layout } from "antd";
import MenuBar from '../../components/menu';
import NavBar from '../../components/navbar';
import SideBar from '../../components/sidebar';
import { Emitter } from "../../utils/emitter";
import { Trans, useTranslation } from "react-i18next";
import { Card, Tabs, Col, Row } from 'antd';

export function DashboardPage() {
    Emitter.EventEmitter.emit(Emitter.Event.Action.SetHeaderTitle, useTranslation().t("sidebar.dashboard"));

    return (
        <div>
            <h1>
                <Trans i18nKey="dashboard.welcome" />
            </h1>
            <div>
                <Tabs
                    defaultActiveKey="1"
                    className="text-white"
                    items={[
                        {
                            label: 'Credenciar Unidades',
                            key: '1',
                            children: <div>
                                <Row align="top" justify="center" gutter={[8, 16]} wrap={true} >
                                    {
                                        [...Array(10)].map((el, index) =>
                                            <Col key={index} style={{ marginBottom: 30 }} xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 6 }}>
                                                <Card title={"card " + (index + 1)}>
                                                    <p>Card content</p>
                                                    <p>Card content</p>
                                                    <p>Card content</p>
                                                </Card>
                                            </Col>)
                                    }

                                </Row>
                            </div>,
                        },
                        {
                            label: 'Visão geral',
                            key: '2',
                            children: <div>
                                {
                                    [...Array(100)].map((el, index) => <h1 key={index}> Line {index + 1} </h1>)
                                }
                            </div>,
                        },
                        {
                            label: 'Perfis de Acesso',
                            key: '3',
                            children: 'Tab 3',
                        },
                        {
                            label: 'Usuários',
                            key: '4',
                            children: 'Tab de Usuários',
                        },
                    ]}
                />
            </div>
        </div>
    );

}