import { Col, Row } from 'antd';
import { Layout } from "antd";
import "./sidebar.scss"
import I18n from "../../utils/i18n/i18n";
import Logo from '../../assets/logo.png'

const SideBar = ({ menu }: any) => {
  return (
    <Layout.Sider
      className="sidebar"
      breakpoint={"lg"}
      theme="dark"
      collapsedWidth={0}
      trigger={null}
      width={250}
      style={{
        overflow: 'hidden',
        height: '100vh',
        position: 'fixed',
        top: 0,
        left: 0,
        overflowX: 'hidden',
        overflowY: 'hidden',
      }}
    >
      <Row align="top" justify="center" gutter={10} className="margin-menu" >
        <Col>
          <img src={Logo} height="30" />
        </Col>
      </Row>
      {menu}
      <I18n />
    </Layout.Sider>
  );
};

export default SideBar;